@import url(//fonts.googleapis.com/css?family=Open+Sans);

body {
  font-family: 'Open Sans';
  color: #343a40;
}

.white-div {
  background-color: #fff;
  padding: 3rem;
  margin: 3rem 7rem;
}

.align-to-end {
  text-align: end;
}

.align-to-center {
  text-align: center;
}

.custom-button-richiesta {
  font-size: large;
  border: 1px solid #45a2f8;
  color: white;
  border-radius: 5px;
  background: #45a2f8;
  margin-top: 2rem;
}

.custom-button-richiesta:hover {
  color: #45a2f8;
  background-color: #efefef;
}

.back-col {
  background-color: #e5e5e5;
}

.title {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 32.8px;
  margin-left: 15rem;
  margin-right: 15rem;
}

.subtitle {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
}

.subtitle-proposta {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
}

.green-check {
  color: #139518;
  width: 88px;
  height: 88px;
}

.full {
  font-family: 'Open Sans';
  padding: 5px;
  color: #45a2f8;
  /* The code that you need to copy */
  height: 100%;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -50px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.toast-bg-error {
  background-color: #cb1a1a !important;
}
